import { PaginationToolbarTemplate } from 'pages/costModels/components/paginationToolbarTemplate';
import { stringify } from 'qs';
import { connect } from 'react-redux';
import { costModelsActions, costModelsSelectors } from 'store/costModels';
const CostModelsPagination = connect((state) => {
    return {
        pagination: costModelsSelectors.pagination(state),
        status: costModelsSelectors.status(state),
        query: costModelsSelectors.query(state),
    };
}, (dispatch) => {
    return {
        fetch: (query) => costModelsActions.fetchCostModels(query)(dispatch),
    };
}, (stateProps, dispatchProps, ownProps) => {
    return {
        itemCount: stateProps.pagination.count,
        perPage: stateProps.pagination.perPage,
        page: stateProps.pagination.page,
        variant: ownProps.variant,
        onSetPage: (_evt, pageNumber) => {
            const offset = (pageNumber - 1) * stateProps.pagination.perPage;
            const newQuery = Object.assign(Object.assign({}, stateProps.query), { offset: offset.toString(), limit: stateProps.pagination.perPage.toString() });
            dispatchProps.fetch(stringify(newQuery));
        },
        // eslint-disable-next-line @typescript-eslint/no-unused-vars
        onPerPageSelect: (_evt, perPage, _page) => {
            const newQuery = Object.assign(Object.assign({}, stateProps.query), { offset: '0', limit: perPage.toString() });
            dispatchProps.fetch(stringify(newQuery));
        },
    };
})(PaginationToolbarTemplate);
export default CostModelsPagination;
